* {
  box-sizing: border-box;
}
body {
  font-family: "Ranchers", cursive;
  margin: 0;
  padding: 0;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.footer-container {
  height: 100%;
  width: 100%;
  background: rgb(14, 58, 41);
}
.container {
  width: 100% !important;
  max-width: 100% !important;
}

.col-md-4 {
  height: 100%;
  padding: 20px 30px;
}
.col-md-4 h2 {
  color: white;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 22px;
}
.col-md-4 p {
  color: white;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 14px;
}
.Tentang h2 {
  text-align: center;
}
.Tentang p {
  text-align: center;
}
.Kontak h2 {
  text-align: center;
}
.Kontak p {
  text-align: center;
}
.copyright {
  background: rgb(9, 38, 27);
  padding: 20px;
}
.copyright h2 {
  text-align: center;
  color: white;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 24px;
}
.copyright span {
  color: tomato;
  cursor: pointer;
}
.copyright span:hover {
  font-weight: 700;
  color: dodgerblue;
}

@media (max-width: 568px) {
  .col-md-4 h2 {
    color: white;
    font-family: "Roboto", sans-serif;
    font-weight: 600;
    font-size: 14px;
  }
  .col-md-4 p {
    color: white;
    font-family: "Roboto", sans-serif;
    font-weight: 300;
    font-size: 12px;
  }
  .Alamat h2,
  p {
    text-align: center;
  }
  .Tentang h2 {
    text-align: center;
  }
  .Tentang p {
    text-align: center;
  }
  .Kontak h2 {
    text-align: center;
  }
  .Kontak p {
    text-align: center;
  }
  .copyright {
    background: rgb(9, 38, 27);
    padding: 20px;
  }
  .copyright h2 {
    text-align: center;
    color: white;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    font-size: 16px;
  }
  .copyright span {
    color: tomato;
  }
}

@-webkit-keyframes beat {
  0% {
    transform: scale(0.75);
  }
  20% {
    transform: scale(1);
  }
  40% {
    transform: scale(0.75);
  }
  60% {
    transform: scale(1);
  }
  80% {
    transform: scale(0.75);
  }
  100% {
    transform: scale(0.75);
  }
}

