.clouds {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
}

.clouds img {
  position: absolute;
  bottom: 5px;
  max-width: 100%;
  -webkit-animation: animate calc(3s * var(--i)) linear infinite;
          animation: animate calc(3s * var(--i)) linear infinite;
}
