.map {
  width: 100%;
  margin: 0;
  padding: 0;
}
iframe {
  height: 80%;
}
.map a {
  position: relative;
  width: 300px;
  font-size: 22px;
  text-decoration: none;
  background: orangered;
  padding: 20px 15px;
  top: 5%;
  left: 30%;
  color: white;
}

.contactList {
  position: relative;
  width: 100%;
  height: 100%;
  align-items: center;
  margin: 0;
  padding: 0;
}

.logoSM {
  position: relative;
  width: 200px;
  color: white;
  opacity: 0.7;
  left: 25%;
  cursor: pointer;
}
.logoSM:hover {
  opacity: 1;
}
.sosmed {
  position: relative;
  width: 100%;
  height: 100%;
  justify-content: center;
  justify-items: center;
}
.sosmed li {
  list-style: none;
  padding: 20px;
}

.follow {
  text-align: center;
  background: orangered;
  color: azure;
}

@media (max-width: 768px) {
  .follow {
    font-size: 40px;
    background: none;
    text-decoration: underline;
  }
  /* .sosmed {
    position: relative;
    top: 20%;
    padding: 30px;
    margin-top: 50px;
    
  } */

  iframe {
    height: 320px;
  }

  .logoSM {
    opacity: 1;
    background: rgba(255, 69, 0, 0.9);
  }
  .map a {
    left: 10%;
  }
}

@media (max-width: 767px) {
  .sosmed {
    position: relative;
    top: 20%;
    padding: 30px;
    margin-top: 80px;
  }
}

@media (max-width: 568px) {
  .follow {
    font-size: 40px;
    background: #ff5722;
    text-decoration: underline;
  }
  .sosmed {
    position: relative;
    top: 20%;
    padding: 30px;
    margin-top: 80px;
  }
  .logoSM {
    left: 5%;
  }
  .map a {
    left: 15%;
  }
  .logoSM {
    opacity: 1;
  }
  .follow {
    background: none;
  }
}

@media (max-width: 375px) {
  .logoSM {
    left: 0%;
    width: 150px;
  }
}
@media (max-width: 340px) {
  .logoSM {
    left: -20%;
  }
  .follow {
    font-size: 28px;
    background: none;
  }
}

